import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import numeral from 'numeral'
import React, { Component, ReactElement, ReactText } from 'react'

import Content, { InterfaceAboutSpec } from '../Data/About'

export default class About extends Component {
    render() {
        return (
            <section className="page page--bgWhite pageAbout">
                <h1 className="page__title pageAbout__title">
                    <span>About me</span>
                    <div className="page__subTitle">
                        <span>Full-stack</span>
                        <span>PHP</span>
                        <span>NodeJS</span>
                        <span>VueJS</span>
                        <span>React</span>
                    </div>
                </h1>
                <section className="page__section pageAbout__section">
                    <div className="row">
                        <div className="col-36 col-md-18 col-lg-12">
                            <img
                                src={process.env.PUBLIC_URL + '/img/profile.jpg'}
                                alt="Bùi Thanh Sơn"
                                className="pageAbout__photo img-fluid"
                                width="100%"
                                height="100%"
                            />
                        </div>
                        <div className="col-36 mt-5 col-md-18 mt-md-0 col-lg-12 pageAbout__desc">
                            <h3>{Content.desc.title}</h3>
                            {this.renderDesc()}
                        </div>
                        <div className="col-36 mt-5 col-md-36 mt-md-5 col-lg-12 mt-lg-0 pageAbout__spec">
                            {this.renderSpec()}
                        </div>
                    </div>
                </section>
                <section className="page__section pageAbout__section pageAbout__sectionService">
                    <h3 className="page__heading">Services</h3>
                    <div className="row">{this.renderService()}</div>
                </section>
                <section className="page__section pageAbout__section pageAbout__sectionFunFact mb-lg-5">
                    <h3 className="page__heading">Fun fact</h3>
                    <div className="row">{this.renderFunFact()}</div>
                </section>
            </section>
        )
    }

    renderDesc() {
        return Content.desc.content.map((dc, i) => {
            return <p key={i}>{dc}</p>
        })
    }

    renderSpec() {
        return Content.spec.map((dl, i) => {
            return (
                <dl className="row" key={i}>
                    <dt className="col-12">
                        <div className="dt">{dl.title}</div>
                    </dt>
                    <dd className="col-24">
                        <div className="dd">{this.reGenerateSpec(dl)}</div>
                    </dd>
                </dl>
            )
        })
    }

    reGenerateSpec(spec: InterfaceAboutSpec) {
        let displayContent: ReactText | ReactElement
        switch (spec.title) {
            case 'Email':
                displayContent = (
                    <a href={`mailto:${spec.content}?subject="Hello from github Iamck CV"`}>{spec.content}</a>
                )
                break
            default:
                displayContent = spec.content
        }

        return displayContent
    }

    renderService() {
        return Content.service.map((service, i) => {
            return (
                <div className="col-36 col-lg-12 card-wrapper mb-3" key={i}>
                    <div className="card">
                        <FontAwesomeIcon icon={service.icon} className="card-img-top" />
                        <div className="card-body">
                            <h4 className="card-title m-0">{service.title}</h4>
                            <div className="card-text d-none">{service.text}</div>
                        </div>
                    </div>
                </div>
            )
        })
    }

    renderFunFact() {
        return Content.funFact.map((funFact, i) => {
            return (
                <div className="col-36 col-lg-18 col-xl-9 card-wrapper mb-3" key={i}>
                    <div className={`card ${i % 2 === 0 ? 'bg__whiteSmoke' : ''}`}>
                        <FontAwesomeIcon icon={funFact.icon} className="card-img-top" />
                        <div className="card-body">
                            <h4 className="card-title">{funFact.title}</h4>
                            <div className="card-text">{numeral(funFact.text).format('0,0')}</div>
                        </div>
                    </div>
                </div>
            )
        })
    }
}
