import React, { Component } from 'react'

import Content, { InterfaceResumeExperience } from '../../Data/Resume'

import Section from './Section'


export default class PdfWork extends Component {
    render() {
        return (
            <Section className="pagePdf__work">
                <div className="row">
                    <div className="col-36 col-lg-9">
                        <h2 className="pagePdf__subTitle">Work experience</h2>
                    </div>
                    <div className="col-36 col-lg-27 mb-3">{this.getGroupCardContent()}</div>
                </div>
            </Section>
        )
    }

    getGroupCardContent() {
        return (
            <div className="pagePdf__workContent">
                {Content.experience.map((item, index) => this.getSimpleContent(item, index))}
            </div>
        )
    }

    getSimpleContent(item: InterfaceResumeExperience, index: number) {
        return (
            <div className="pagePdf__workContentItem" key={index}>
                <div className="card">
                    <div className="card-body pagePdf__workInfo">
                        <div className="card-title pagePdf__workTitle">
                            <div className="pagePdf__workCompanyName">{item.subTitle}</div>
                            <div className="pagePdf__workCompanyPosition">{item.title}</div>
                            <div className="pagePdf__workDate d-none d-sm-block float-right">{item.time}</div>
                        </div>
                        <ul>
                            {item.description.map((d, di) => (
                                <li key={di}>{d}</li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}
