import React, { Component, ReactElement } from 'react'

interface InterfacePdfSectionProps {
    className: string
    children: string | ReactElement
}

interface InterfacePdfSectionState {}

export default class PdfSection extends Component<InterfacePdfSectionProps, InterfacePdfSectionState> {
    render() {
        return <section className={`pagePdf__section ${this.props.className}`}>{this.props.children}</section>
    }
}
