import React, { Component } from 'react'

import Content, { InterfaceQualification } from '../../Data/Qualification'

import Section from './Section'

export default class PdfQualification extends Component {
    render() {
        return (
            <Section className="pagePdf__qualification">
                <div className="row">
                    <div className="col-36 col-lg-9">
                        <h2 className="pagePdf__subTitle">Qualification</h2>
                    </div>
                    <div className="col-36 col-lg-27">{this.renderQualificationBlock()}</div>
                </div>
            </Section>
        )
    }

    renderQualificationBlock() {
        return Content.map((item, index) => {
            return (
                <div className="itemQualification" key={index}>
                    <h4 className="itemQualification__title mt-3 mt-lg-0">{item.title}</h4>
                    <ul>{this.renderQualificationItem(item)}</ul>
                </div>
            )
        })
    }

    renderQualificationItem(item: InterfaceQualification) {
        return item.content.map((i, index) => {
            return <li key={index}>{i}</li>
        })
    }
}
