import React, { Component } from 'react'

import PdfContact from '../Component/Pdf/Contact'
import PdfHeader from '../Component/Pdf/Header'
import PdfProject from '../Component/Pdf/Project'
import PdfQualification from '../Component/Pdf/Qualification'
import PdfWork from '../Component/Pdf/Work'

export default class Pdf extends Component {
    render() {
        return (
            <section id="pagePdf" className="pagePdf pagePdf__container container px-3 px-sm-5">
                <PdfHeader />
                <PdfContact />
                <PdfQualification />
                <PdfWork />
                <PdfProject />
            </section>
        )
    }

    componentDidMount() {
        const pagePdf = String(document.querySelector('.content__page')?.innerHTML)
        document.querySelector('#mainNavigation')?.remove()
        document.querySelector('#mainContent')?.remove()

        document.querySelector('#root')!.innerHTML = pagePdf
        document.body.setAttribute('id', 'pagePdf')
    }
}
