import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'

import ListRouter, { InterfaceConfigRouter } from '../Config/Router'

export default class Navigation extends Component {
    render() {
        return (
            <div className="row">
                <div className="col-md-18 col-lg-12 d-none d-md-block">
                    <img
                        src={process.env.PUBLIC_URL + '/img/profile.jpg'}
                        alt="Bùi Thanh Sơn"
                        className="navigation__photo "
                        width="60"
                        height="60"
                    />
                    <h1 className="navigation__name d-none d-md-block">Bùi Thanh Sơn</h1>
                </div>
                <ul className="nav navigation__mainNav col-36 col-md-12">{this.renderItem()}</ul>
            </div>
        )
    }

    renderItem() {
        return ListRouter.map((router: InterfaceConfigRouter, i: number) => {
            return (
                <li className="nav-item navigation__item" key={i}>
                    <NavLink
                        className="nav-link navigation__link"
                        activeClassName="active"
                        to={router.path}
                        id={router.component}
                        exact
                        strict
                    >
                        <span className="navigation__icon">
                            <FontAwesomeIcon icon={router.icon} />
                        </span>
                        <span className="navigation__title">{router.title}</span>
                    </NavLink>
                </li>
            )
        })
    }
}
