import React, { Component, ComponentClass } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'

import ConfigRouter from '../Config/Router'
import PageAbout from '../Page/About'
import PageContact from '../Page/Contact'
import PageHome from '../Page/Home'
import PagePdf from '../Page/Pdf'
import PageProject from '../Page/Project'
import PageResume from '../Page/Resume'

const Components: { [page: string]: ComponentClass } = {
    Home: PageHome,
    About: PageAbout,
    Resume: PageResume,
    Project: PageProject,
    Contact: PageContact,
}

export default class RouterContent extends Component {
    render() {
        return (
            <Switch>
                {ConfigRouter.map((r, k) => (
                    <Route exact key={k} path={r.path} component={Components[r.component]} />
                ))}
                <Route exact path="/pdf" component={PagePdf} />
                <Redirect from="/*" to="/" />
            </Switch>
        )
    }
}
