import React, { Component } from 'react'
import { Link } from 'react-router-dom'

import Content from '../Data/Resume'

export default class Resume extends Component {
    render() {
        return (
            <section className="page page--bgWhite pageResume">
                <h1 className="page__title pageResume__title">
                    <span>Resume</span>
                    <div className="page__subTitle">
                        <span>+6 years experience</span>
                    </div>
                </h1>
                <div className="row">
                    <div className="col-36 col-md-18 col-lg-12">
                        <h3 className="page__heading">Education</h3>
                        <div className="pageResume__timeline">{this.renderEducation()}</div>
                    </div>
                    <div className="col-36 col-md-18 col-lg-12">
                        <h3 className="page__heading">Experience</h3>
                        <div className="pageResume__timeline">{this.renderExperience()}</div>
                    </div>
                    <div className="col-36 col-md-36 col-lg-12">
                        <h3 className="page__heading">Skills</h3>
                        <div className="pageResume__skill">{this.renderSkill()}</div>
                    </div>
                    <div className="col-36 my-3 text-center">
                        <Link to="/pdf" target="_blank" className="btn btn-outline-primary">
                            Download CV
                        </Link>
                    </div>
                </div>
            </section>
        )
    }

    renderEducation() {
        return Content.education.map((education, i) => {
            return (
                <div className="pageResume__timelineItem" key={i}>
                    <div className="pageResume__timelineTime">{education.time}</div>
                    <div className="pageResume__timelineTitle">{education.title}</div>
                    <div className="pageResume__timelineSubTitle">{education.subTitle}</div>
                    <div className="pageResume__timelineDesc">{education.description}</div>
                </div>
            )
        })
    }

    renderExperience() {
        return Content.experience.map((experience, i) => {
            let strDesc
            if (typeof experience.description === 'string') {
                strDesc = <li>{experience.description}</li>
            } else {
                strDesc = experience.description.map((descItem, i) => {
                    return <li key={i}>{descItem}</li>
                })
            }

            return (
                <div className="pageResume__timelineItem" key={i}>
                    <div className="pageResume__timelineTime">{experience.time}</div>
                    <div className="pageResume__timelineTitle">{experience.title}</div>
                    <div className="pageResume__timelineSubTitle">{experience.subTitle}</div>
                    <ul className="pageResume__timelineDesc">{strDesc}</ul>
                </div>
            )
        })
    }

    renderSkill() {
        return Content.skill.map((skill, i) => {
            let skillPercentage: number = skill.percentage + Math.random() * 5

            return (
                <div className="pageResume__skillItem" key={i}>
                    <div className="pageResume__skillTitle">{skill.title}</div>
                    <div className="progress pageResume__skillProgress">
                        <div
                            className="progress-bar progress-bar-striped progress-bar-animated pageResume__skillProgressBar"
                            role="progressbar"
                            aria-valuemin={0}
                            aria-valuemax={100}
                            aria-valuenow={skillPercentage}
                            style={{ width: `${skillPercentage}%` }}
                        />
                    </div>
                </div>
            )
        })
    }
}
